@import url('https://fonts.googleapis.com/css2?family=Alata&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Crimson+Text&display=swap');

.header{
    display: flex;
    align-items: center;
    color: white;
    background-color: black;
    top: 0;
    position: sticky;
    z-index: 2000;
}
.header_left{
    flex: 0.4;
}
    
.header_left > h1 {
    text-align: center;
    font-family: "alata",sans-serif;
    letter-spacing: 1px;
    padding: 20px 0;
}

.header_left>h1>span{
    color:red;
}

.header_right{
    display: flex;
    flex:0.6;
    justify-content: space-evenly;
    align-items: center;
}

.header_right>a>h4 {
    font-size: 18px;
    cursor: pointer;
    transition: transform 200ms ease-in-out;
    font-weight: "Crimson Text",serif;
    padding: 20px 0;
    letter-spacing: 1px;
}
.header_right>a>h4:hover{ 
    transform: scale(1.09);
    background-color: red;
    color: white;
    padding: 5px 20px;
    letter-spacing: 1px;
    font-family: "Crimson Text",serif;
    font-size: 18px;
    cursor: pointer;
}
.right_headerbutton{
    background-color: red;
    color: white;
    padding: 5px 20px;
    border-radius: 2px;
    cursor: pointer;
    
  }